<template>
  <div id="app">
    <header class='header'>
    </header>
    <main class='main'>
      <div class='photos'>
        <div class='photo active'>
          <template v-if='active < 15'>
            <img class='img' :src='src'
              :class='classTrans' :key='active'
            />
            <p class='text'>{{text}}
            </p>
          </template>
          <template v-else>
            <div class='img' :class='classTrans' :key='active'
              style='width:100%;height:100%;line-height: 60vh;'
            >
              <p class='text' v-html='text'></p>
            </div>
          </template>
        </div>
      </div>
    </main>
    <footer class='footer'>
      <audio @canplay="myOnCanPlayFunction"
        ref='Music'
        muted
        id='bgmusic'
        preload 
        :src='require("./media/music.ogg")'
        loop
      />
      <img src='./assets/day.png' class='day' />
    </footer>
  </div>
</template>

<script>
/**
20200416 计划：
完成左右移动的动画效果
图片压缩

 */
export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      canPlay: false,
      photos: [],
      active: 1,
      transformX: 0,
      classTrans: '',
      texts: [
        '结巴，生日快乐！',
        '没错，这就是盗用你的图，hiahiahia\n（〃｀ 3′〃）',
        '虽然你自己存的图片肯定比我多啦',
        '而且肯定比我保存的要好看',
        '不过我手里好像就这些啦',
        '所以来个结巴照片展',
        'look，这阳光灿烂的你\n赛高(≧∇≦)ﾉ',
        'look，这可爱的你',
        'look，这还是可爱的你',
        '咦，好像混进来什么奇怪的东西\n(○｀ 3′○))',
        '不要妄自菲薄',
        '你是很优秀的人',
        '然而我也是\nε=ε=ε=(~￣▽￣)~',
        'time through everything\ntime sees everything',
        '最后送上一首歌......',
        '后面没啦.......',
        '真的没有啦！！！不要再翻啦！！！',
        '你咋就不信呢.......',
        '我看你能翻到什么时候去',
        '...',
        '...',
        '...',
        '...',
        '好吧，你赢了',
        '然而这里只有一句话',
        '<strong>生日快乐</strong>'
      ]
    }
  },
  computed: {
    src () {
      let t = this.photos.find(o => o.name === this.active)
      return t && t.src
    },
    text () {
      return this.texts[this.active - 1]
    }
  },
  created () {
    for(let i=1;i<=14;i++){
      this.photos.push({ name: i, src: require(`./assets/${i}.jpg`)})
    }
  },
  mounted () {
    let dom = document.querySelector('.img')
    this.bind(dom)
    let audio = document.getElementById('bgmusic');
    function musicInBrowserHandler() {
      try{
        setTimeout(() => {
          audio.play();
          audio.volume = 1;
          document.body.removeEventListener('touchstart', musicInBrowserHandler);
        }, 0)
      } catch (error) {
        console.log(error)
      }
    }
    document.body.addEventListener('touchstart', musicInBrowserHandler)
  },
  methods: {
    myOnCanPlayFunction () {
      this.canPlay = true
    },
    bind (ele) {
      let start = 0
      let end = 0
      let result = 0
      let func = evt => {
        if (start === 0) start = evt.targetTouches[0].pageX
        end = evt.targetTouches[0].pageX
        result = this.transformX = end - start
        evt.target.style.cssText=`transform: translateX(${result}px);`
      }
      ele.addEventListener('touchstart', () => {
        start = 0
        end = 0
      })
      ele.addEventListener('touchmove', func)
      ele.addEventListener('touchend', (evt) => {
        if (start - end > 50 && this.active < 26) {
          this.active++
          this.classTrans = 'right'
        } else if (end - start > 50 && this.active > 1) {
          this.active--
          this.classTrans = 'left'
        } else {
          evt.target.style.cssText=`transform: translateX(0px);`
        }
        this.$nextTick(() => {
          let dom = document.querySelector('.img')
          this.bind(dom)
        })
      })
    }
  }
}
</script>

<style>
@keyframes left{
  from { transform: translateX(-50px) ;}
  to { transform: translateX(0);}
}
@keyframes right{
  from { transform: translateX(50px);}
  to { transform: translateX(0);}
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: url('./assets/back2.jpg') no-repeat;
    background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.header{
  flex: 0 1 auto;
}
.main{
  flex: 1;
  padding: 20px 10px;
}
.footer{
  flex: 0 1 auto;
}
.photo.active{
  width: 100%;
  height: 100%;
  display: inline-block;
}
.photos{
  width: 80vw;
  height: 60vh;
  background-color: #fff;
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
}
.img{
  width: 80%;
  height: 80%;
}

.day{
  width: 200px;
  height: 18vh;
}
.img.left{
  animation: left .5s;
}
.img.right{
  animation: right .5s;
}
.text{
  margin-top: 10px;
  font-size: 16px;
  font-family: FangSong;
  white-space: pre-line;
}
</style>
